<template>
    <TopNav title="Pulse" iconClass="bi bi-activity">
        <template #nav-buttons>
            <button class="btn py-0 my-0 px-1 mx-1" type="button" @click="fetchAll">
                <i class="bi bi-arrow-clockwise fs-4"></i>
            </button>
        </template>
    </TopNav>
    <div class="container">
        <div v-if="items.length>0">
            <div v-for="item in sortedItems" :key="item.uniqueId" class="card shadow mb-2">
                <img :src="item.imageUrl" class="card-img-top" style="max-height: 200px;object-fit: cover;object-position: 50% 20%;">
                <div class="card-body">
                    <div class="d-flex align-items-center flex-wrap mb-1">
                        <span class="badge text-truncate text-bg-primary m-1">{{ item.feedName }}</span>
                        <span class="badge text-bg-warning m-1">{{ item.category }}</span>
                        <span class="text-muted m-1">{{ Calendar.formatRelativeTime(item.date) }}</span>
                    </div>
                    <a :href="item.link || '#'" target="_blank" class="card-title fs-5">{{ item.title }}</a>
                    <p class="card-text">{{ item.description }}</p>
                </div>
            </div>
        </div>
        <!-- PLACEHOLDERS -->
        <div v-if="items.length===0">
            <div v-for="index in 10" :key="index" class="card shadow mb-2">
                <img src="" class="card-img-top placeholder">
                <div class="card-body">
                    <a class="btn btn-primary disabled placeholder col-1 mb-2" aria-disabled="true" style="height: 10px;"></a>
                    <a class="btn btn-warning disabled placeholder col-1 ms-2 mb-2" aria-disabled="true" style="height: 10px;"></a>
                    <h5 class="card-title placeholder-glow">
                        <span class="placeholder col-6"></span>
                    </h5>
                    <p class="card-text placeholder-glow">
                        <span class="placeholder col-7"></span>
                        <span class="placeholder col-4"></span>
                        <span class="placeholder col-4"></span>
                        <span class="placeholder col-6"></span>
                        <span class="placeholder col-8"></span>
                    </p>
                    <a class="btn btn-primary disabled placeholder col-6" aria-disabled="true"></a>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
// IMPORTS
// -------
import { ref, onMounted, computed } from 'vue';
import TopNav from './TopNav.vue';
import { Calendar } from '@/Tools';

// STATIC PROPERTIES
// -----------------
const defaultFeeds = [
    {url: "https://www.wired.com/feed/rss", category:"Tech"},
    {url: "https://www.theverge.com/rss/index.xml", category:"Tech"},
    {url: "https://feeds.bbci.co.uk/news/world/rss.xml", category:"News"},
    {url: "https://www.dr.dk/nyheder/service/feeds/senestenyt", category:"News"},
    {url: "https://www.dr.dk/nyheder/service/feeds/indland", category:"News"},
    {url: "https://www.aljazeera.com/xml/rss/all.xml", category:"News"}
];
const rssItemTags = {
    title:["title"],
    link:["link"],
    description:["description","summary"],
    date:["pubDate", "published"],
    id:["guid", "id"],
    category:["category"]
}
const imageTags = [
    {tag:"media:thumbnail", attr:"url"},
    {tag:"media:content", attr:"url"},
]

// REACTIVE PROPERTIES
// -------------------
const feeds = ref([]);
const items = ref([]);
const tempItems = ref([]);

// COMPUTED PROPERTIES
// -------------------
const sortedItems = computed(()=>{
    return [...items.value].sort((a,b) => Calendar.new(b.date)-Calendar.new(a.date));
})

// METHODS
// -------
const loadFeeds = () => {
    feeds.value = JSON.parse(localStorage.getItem("rssFeeds")) || defaultFeeds;
}

const fetchRss = async (feed) => {
    try {
        const response = await fetch(`https://api.allorigins.win/get?url=${encodeURIComponent(feed.url)}`);
        const data = await response.json();
        const parser = new DOMParser();
        const imgPattern = /.+src="(http\S+)"\s.+/;
        let content = data.contents;

        if (content.includes("base64,")) {
            const binaryString = atob(content.split(",")[1])
            const uint8Array = new Uint8Array(binaryString.length);
            for (let i = 0; i < binaryString.length; i++) {
                uint8Array[i] = binaryString.charCodeAt(i);
            }
            const decoder = new TextDecoder("utf-8");
            content = decoder.decode(uint8Array);
        }

        if (feed.url === "https://www.rte.ie/feeds/rss/?index=/news/&limit=20") {
            console.log(content);
        }

        try {
            const xmlDoc = parser.parseFromString(content, 'text/xml');
            const titleElm = xmlDoc.querySelector('title');
            const rssTitle = titleElm ? titleElm.textContent || titleElm.innerText : null;
            let rssItems = xmlDoc.querySelectorAll('item')
            if (rssItems.length===0) {
                rssItems = xmlDoc.querySelectorAll('entry')
            }
            rssItems.forEach(item => {
                const rssItem = {feedName:rssTitle, uniqueId:crypto.randomUUID()};

                Object.entries(rssItemTags).forEach(([tag, alts])=>{
                    for (const alt of alts) {
                        const elm = item.getElementsByTagName(alt);
                        if (elm.length>0) {
                            rssItem[tag] = elm[0].textContent;
                            break;
                        }

                    }
                });

                for (const tag of imageTags) {
                    const elm = item.getElementsByTagName(tag.tag);
                    if (elm.length>0) {
                        const url = elm[0].getAttribute(tag.attr);
                        if (url.includes("http")) {
                            rssItem.imageUrl = url;
                            break;
                        }
                    }
                }

                if (!rssItem.imageUrl) {
                    const imgMatch = imgPattern.exec(item.innerHTML);
                    if (imgMatch) {
                        rssItem.imageUrl = imgMatch.at(1);
                    }
               
                }

                if (!rssItem.category || rssItem.category === "") {
                    rssItem.category = feed.category;
                }

                if (rssItem.description && rssItem.description.includes("&#")) {
                    const tempDiv = document.createElement("div");
                    tempDiv.innerHTML = rssItem.description;
                    rssItem.description = tempDiv.textContent || tempDiv.innerText || rssItem.description;
                }

                tempItems.value.push(rssItem);
            })
        } catch (err) {
            console.log("Failed to parse XML document from " + feed.url + ". Error: " + err)
        }
    } catch (err) {
        console.log('Failed to fetch RSS reed: ' + feed.url);
    }
}

const fetchAll = () => {
    items.value = [];
    tempItems.value = [];
    for (const feed of feeds.value) {
        fetchRss(feed);
    }
    setTimeout(() => {
       items.value = tempItems.value; 
       tempItems.value = [];
    }, 500);
}

// LIFECYCLE HOOKS
// ---------------
onMounted(()=>{
    loadFeeds();
    fetchAll();
})

</script>

<style scoped>
img.placeholder {
    height: 200px;
    width: 100%;
    background-color: var(--bs-gray-100);
    border-width: 0;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
}
.badge {
    overflow: hidden;
}
</style>